import Vue from 'vue'
import Router from 'vue-router'
import Keynotes from '@/components/Keynotes'
import Program from '@/components/Program'
import Sunday from '@/components/days/Sunday'
import Sponsors from '@/components/Sponsors'
import Contact from '@/components/Contact'

Vue.use(Router)

export default new Router({
  routes: [
   
    {
      path: '/keynotes',
      name: 'Keynotes',
      component: Keynotes
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    },
  {
      path: '/program',
      name: 'Program',
      component: Program
    },
    {
      path: '/program/sunday',
      name: 'Sunday',
      component: Sunday
    },
    {
      path: '/sponsors',
      name: 'Sponsors',
      component: Sponsors
    },

  ]
})



