<template lang='pug'>
v-app
  v-container(fluid class='top')
    v-layout(fluid text-xs-center align-center justify-center row wrap)
      v-flex(id='box' xs12 sm12 md10 lg10)
        br
        img(src='./assets/logo.png' id='logo')
        br
        br
        h1 The Marine Biological Association 
        h1 16th Annual Postgraduate Conference
        h2 5th - 8th May 2019 &nbsp; &nbsp; · &nbsp; &nbsp; Newcastle University
        br
        v-btn( large color="base" dark) Sign up
        br
  v-container(fluid class="white")
    v-layout(fluid text-xs-left align-center justify-center row wrap)
      v-flex( xs11 sm10 md10 lg10)
        //- h1 About
        br
        h1(class='text-xs-center') The Marine Biological Association’s 16th Annual Postgraduate Conference is taking place in May 2019 at Newcastle University. 
        br
        p The MBA Postgraduate Conference has a reputation for providing a welcoming and open platform where postgraduates can share their marine science research. This year will be no different as the conference will deliver the same friendly and encouraging environment but with a touch of the famous Northern hospitality. 
        p The conference will showcase current postgraduate research and encourage networking, not only within the postgraduate community, but also with senior academics and leaders in the marine science industry. We aim to bring together marine scientists, stimulate discussion about ongoing research and generate ideas that may lead to future collaborations. By liaising with local businesses and industry leaders in the marine sector, we also encourage those that attend to think about future career options.  
        p This conference is open to all current postgraduates/postgraduates within three years of graduation/early-career scientists (i.e. research assistants). 
        p At this conference we are committed to sustainability, therefore we are going paperless! Daily schedules are available to be downloaded below and keep up to date with all conference details on our social media pages. Our merch is eco-friendly and only reusable alternatives to single use plastics will be available. Wherever possible we’ll be donating leftover food to local food banks too.
        br
      
  v-container(fluid class="base")
    v-layout(fluid text-xs-left align-center justify-center row wrap)
      v-flex(xs11 sm10 md10 lg10)
        br
        h1(class='text-xs-center' id='social') #MBA19
        h2(class='text-xs-center') Follow us on social media:
        v-container(text-xs-center)
          v-layout
            v-flex(xs12 lg3)
              a(href='mailto:mba2019@newcastle.ac.uk')
                i(class='fas fa-envelope fa-4x' id='icon')
                h2 Email 
                
            v-flex(xs12 lg3)
              a(href='https://www.facebook.com/mbaconference19/')
                i(class='fab fa-facebook fa-4x'  id='icon')
                h2 Facebook

            v-flex(xs12 lg3)
              a(href='https://www.twitter.com/mbaconference19/')
                i(class='fab fa-twitter fa-4x' id='icon')
                h2 Twitter

            v-flex(xs12 lg3)
              a(href='https://www.instagram.com/mbaconference19/')
                i(class='fab fa-instagram fa-4x' id='icon')
                h2 Instagram

  v-container(fluid class="white")
    v-layout(fluid text-xs-left align-center justify-center row wrap)
      v-flex(xs11 sm10 md10 lg10)
        br
        h1 Full program
        br
        p The conference will begin officially at <strong>8:30am on Monday 6th May</strong>, but the 5th May is a cracking opportunity for a day tour of the Northumberland Coast alongside a Northumberland Inshore Fisheries and Conservation Authority representative! 
        br
        h2(class='text-xs-center') Sunday 5th May
        br
        p 40 places are available on the coach, allowing delegates to see the beauty that the North East has to offer. We’ll be taking you to the seaside town of Amble for a tour of Amble Lobster Hatchery, followed by some traditional fish and chips. Then we’re all off for a short stop at St Mary’s Lighthouse at Blyth and finishing at the Newcastle University Dove Marine Laboratory. The evening will feature a presentation by our NIFCA representative, a canapés and drinks reception, and a pub quiz! The coach will then take us all back to the city afterwards. Be sure to sign up when tickets go on sale in December! It’ll be a great way to get comfortable in the North East and meet the team and other delegates!  
        br
        p PDFs containing the full program to follow.
        br
        v-container(text-xs-center)
          v-layout
            v-flex(xs12 lg4)
              h2 Monday 6th May
            v-flex(xs12 lg4)
              h2 Tuesday 7th May
            v-flex(xs12 lg4)
              h2 Wednesday 8th May
  v-container(fluid class="dark")
    v-layout(fluid text-xs-left align-center justify-center row wrap)
      v-flex(xs11 sm10 md10 lg10)
        br
        h1 Ticket information
        br
        p(class='text-xs-center') Non-member &nbsp; &nbsp; · &nbsp; &nbsp; £85
        p(class='text-xs-center') MBA member &nbsp; &nbsp; · &nbsp; &nbsp; £76.50
        p(class='text-xs-center') Early bird &nbsp; &nbsp; · &nbsp; &nbsp; £75
        p(class='text-xs-center') Early bird MBA member  &nbsp; &nbsp; · &nbsp; &nbsp; £67.50
        p(class='text-xs-center') MBA Student Ambassador  &nbsp; &nbsp; · &nbsp; &nbsp; £42.50
        p Early bird rates available until <strong> confirm date</strong>.
        p All attending delegates will receive a free one year membership to the Marine Conservation Society.  
  v-container(fluid class="white")
    v-layout(fluid text-xs-left align-center justify-center row wrap)
      v-flex(xs11 sm10 md10 lg10)
        br
        h1 Keynotes
        br
        h2 MBA Director
        p Details to follow.
        h2 Professor Nick Polunin <em>(Newcastle University)</em>
        div
          //- img(src='./assets/nick.jpg' style="vertical-align:middle" id='logo')
          p A Marine Conservation Ecologist researching and lecturing ecosystem connectivity at Newcastle University, including reef fish-habitats and sustainable fisheries. Nick is also an editor for the Cambridge University Press and hugely involved with the Northumberland Inshore Fisheries and Conservation Authority (NIFCA).
        h2 Dr Amy Lusher <em>(Norwegian Institute for Water Research)</em>
        p Details to follow.
        h2 Dr Claire Hellio <em>(University of Western Brittany)</em>
        div
          //- img(src='./assets/claire.jpg')
          p A Marine Biotechnology Professor researching at the University of Western Brittany focusing on anti-fouling bioactive compounds, director of biodimar and European Society for Marine Biotechnology representative. 
  v-container(fluid class="base")
    v-layout(fluid text-xs-left align-center justify-center row wrap)
      v-flex(xs11 sm10 md10 lg10)
        br
        h1 Workshops
        br
        h2 Duncan Yellowlees
        p We are so pleased to host a fantastic communication workshop this year with the talented speaker, <a href='http://duncanyellowlees.com'> Duncan Yellowlees</a>. Helping us turn our ideas into engaging and interesting presentations, Duncan will train us in the art of communicating logically and emotionally, person to person. Forget boring presentations even existed! It’s time we get inspired! This workshop will take place in the afternoon of Monday 6th May 2019 at 16:30. 
        
  v-container(fluid class="white")
    v-layout(fluid text-xs-left align-center justify-center row wrap)
      v-flex(xs11 sm10 md10 lg10)
        br
        h1 Sponsors

        v-container(text-xs-center)
          v-layout(row wrap)
            v-flex(xs12 sm12 md4 lg4)
              img(src='./assets/mcs.jpg' id='sponsor')
              h2 Marine Conservation Society
              p Details to follow.
            v-flex(xs12 sm12 md4 lg4)
              img(src='./assets/nifca.jpg' id='sponsor')
              h2 Northumberland Inshore Fisheries Conservation Authority
              p Details to follow.
            v-flex(xs12 sm12 md4 lg4)
              img(src='./assets/pouk.jpg' id='sponsor')
              h2 Planet Ocean UK
              p Details to follow.
           
</template>
 

<script>
export default {
  data() {
    return {
      // Change the name of the project
      title: "MBA PG Conference 2019",
      pages: [
        { name: "Home", link: "/" },
        { name: "About", link: "/about" },
        { name: "Keynotes and workshops", link: "/keynotes" },
        { name: "Program", link: "/program" },
        { name: "Sponsors", link: "/sponsors" },
        { name: "Contact", link: "/contact" }
      ]
    };
  }
};
</script>
 


